<template>
  <div class="DraftEdit">
    <div class="header-bar">
      <h3 class="header">Панель администратора теста</h3>
      <Button class="exit-btn" size="small" @click="$store.commit('setLoggedIn', false)">Выход</Button>
    </div>
    <div class="nav">
      <DraftNav
        v-if="$store.state.allDrafts"
        :itemsList="$store.state.allDrafts"
        :defaultValue="selectedDraft"
        @select="handleChange"
      />
    </div>
    <Editor />
    <div class="save-btn-wrapper">
      <Button
        class="save-btn"
        type="primary"
        :loading="saving"
        @click="handleSave"
        :disabled="$store.state.currentDraftName === '(active_draft)'"
      >Опубликовать</Button>
      <div class="comment-wrapper" v-show="error">
        <a-icon class="comment-icon close-circle" type="close-circle" />Возникла ошибка
      </div>
      <!-- <transition duration="1000" name="slide-fade" v-if="success">-->
      <div class="comment-wrapper" v-show="success">
        <a-icon class="comment-icon check-circle" type="check-circle" />Сохранено
      </div>
      <!-- </transition>-->
      <!-- {{normal}}
      {{saving}}
      {{error}}
      {{success}}-->
    </div>
  </div>
</template>

<script>
import { Button, Icon } from "ant-design-vue";
import DraftNav from "@/components/DraftNav";
import Editor from "@/components/Editor";

export default {
  name: "DraftEdit",
  components: { Button, DraftNav, Editor, Icon },
  data() {
    return {
      selectedDraft: "prod.yml",
      saveBtnType: "primary",
      saveBtnText: "Опубликовать"
    };
  },
  async mounted() {
    await this.$store.dispatch("getAllDrafts");
    this.$store.commit("setCurrentDraftName", this.selectedDraft);
    this.selectedDraft !== "" &&
      this.$store.dispatch("getDraft", this.selectedDraft);
  },
  props: [],
  computed: {
    normal() {
      return this.$store.state.saveDraftStatus === "normal";
    },
    saving() {
      return this.$store.state.saveDraftStatus === "loading";
    },
    error() {
      return this.$store.state.saveDraftStatus === "error";
    },
    success() {
      return this.$store.state.saveDraftStatus === "success";
    }
  },
  methods: {
    handleChange(v) {
      this.selectedDraft = v;
      this.$store.dispatch("getDraft", this.selectedDraft);
    },
    handleDraftTextUpdate() {},
    async handleSave() {
      await this.$store.dispatch("saveDraftRemote", this.selectedDraft);
      setTimeout(
        () => this.$store.commit("setSaveDraftStatus", "normal"),
        2000
      );
      // this.$store.commit("setSaveDraftStatus", "normal");
      console.log(this.$store.state.saveDraftStatus);
    }
  }
};
</script>

