<template>
  <prism-editor
    v-model="draftText"
    :code="$store.state.draftFile.text"
    language="yaml"
    :readonly="$store.state.currentDraftName === '(active_draft)'"
  ></prism-editor>
</template>

<script>
import "prismjs";
import "prismjs/themes/prism.css";
import PrismEditor from "vue-prism-editor";
export default {
  name: "Editor",
  components: {
    PrismEditor
  },
  computed: {
    draftText: {
      get() {
        return this.$store.state.draftFile.text;
      },
      set(v) {
        this.$store.commit("updateDraftText", v);
      }
    }
  }
};
</script>