<template>
  <a-radio-group
    v-if="itemsList"
    @change="handleChange"
    :defaultValue="defaultValue"
    buttonStyle="solid"
    size="small"
  >
    <a-radio-button v-for="item in itemsList" :key="item" :value="item">{{item}}</a-radio-button>
  </a-radio-group>
</template>

<script>
import { Radio } from "ant-design-vue";

export default {
  name: "DraftNav",
  components: { Radio },
  data() {
    return {
      value: ""
    };
  },
  props: ["itemsList", "defaultValue"],
  methods: {
    handleChange(e) {
      this.value = e.target.value;
      this.$emit("select", e.target.value);
      this.$store.commit("setCurrentDraftName", this.value);
    }
  }
};
</script>

