import Vue from "vue";
import App from "./App.vue";
import store from "./store";
import {
  Button,
  Radio,
  message,
  Alert,
  Icon,
  Form,
  Input
} from "ant-design-vue";
import VueCookies from "vue-cookies";

Vue.use(Button);
Vue.use(Radio);
Vue.use(Alert);
Vue.use(Icon);
Vue.use(Form);
Vue.use(Input);

Vue.use(VueCookies);
Vue.prototype.$form = Form;
Vue.prototype.$message = message;
Vue.config.productionTip = false;
window.store = store.state;

new Vue({
  store,
  render: h => h(App)
}).$mount("#app");
