<template>
  <div id="app">
    <DraftEdit v-if="$store.state.loggedIn" />
    <Login v-else />
  </div>
</template>

<script>
import DraftEdit from "@/components/DraftEdit.vue";
import Login from "@/components/Login";

export default {
  name: "app",
  components: {
    DraftEdit,
    Login
  },
  created() {
    if (this.$cookies.get("yreaders") === "true") {
      this.$store.commit("setLoggedIn", true);
    }
  }
};
</script>

