import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

const api = axios.create({
  baseURL: process.env.VUE_APP_ROOT_API
});
api.interceptors.response.use(null, error => {
  if (!error.response && error.response.status === 401) {
    store.commit("setLoggedIn", false);
    window.$cookies.remove("yreaders");
    window.$cookies.remove("yreaders_key");
  }
  return Promise.reject(error);
});
export default new Vuex.Store({
  state: {
    loggedIn: false,
    apikey: "",
    draftFile: {},
    allDrafts: [],
    saveDraftStatus: "normal",
    currentDraftName: ""
  },
  mutations: {
    setLoggedIn(state, key) {
      state.loggedIn = key;
      window.$cookies.set("yreaders", key);
    },
    setApiKey(state, key) {
      state.apikey = key;
      window.$cookies.set("yreaders_key", key);
    },
    setAllDrafts(state, draftList) {
      state.allDrafts = draftList;
    },
    setDraftFile(state, draftFile) {
      state.draftFile = draftFile;
    },
    setSaveDraftStatus(state, status) {
      state.saveDraftStatus = status;
    },
    updateDraftText(state, text) {
      state.draftFile.text = text;
    },
    setCurrentDraftName(state, name) {
      state.currentDraftName = name;
    }
  },
  actions: {
    async getApiKey({ commit }, loginData) {
      try {
        const r = await api.post("/api/sys/key", {
          ...loginData
        });
        commit("setApiKey", r.data["x-api-key"]);
        commit("setLoggedIn", true);
      } catch (error) {
        commit("setLoggedIn", false);
        console.error(error);
      }
    },
    async updateApiKey({ state, commit }) {
      try {
        const r = await api("/api/sys/key", {
          headers: {
            "x-api-key": state.apikey || window.$cookies.get("yreaders_key")
          }
        });
        commit("setApiKey", r.data["x-api-key"]);
        commit("setLoggedIn", true);
      } catch (error) {
        commit("setLoggedIn", false);
        console.error(error);
      }
    },
    async getAllDrafts({ state, dispatch, commit }) {
      try {
        await dispatch("updateApiKey");
        const r = await api("/api/sys/drafts", {
          headers: { "x-api-key": state.apikey }
        });
        commit("setAllDrafts", r.data);
      } catch (error) {
        console.error(error);
      }
    },
    async getDraft({ state, dispatch, commit }, draftName) {
      try {
        commit("setDraftFile", { text: "Загрузка..." });
        await dispatch("updateApiKey");
        const url = `/api/sys/drafts/${draftName}`;
        const r = await api(url, {
          headers: { "x-api-key": state.apikey }
        });
        commit("setDraftFile", r.data);
        console.log(r.data);
      } catch (error) {
        commit("setDraftFile", { text: "Ошибка" });
        console.error(error);
      }
    },
    async setActiveDraft({ state, dispatch }, draftName) {
      try {
        await dispatch("updateApiKey");
        const url = `/api/sys/drafts/active/${draftName}`;
        await api.patch(url, null, {
          headers: { "x-api-key": state.apikey }
        });
        return Promise.resolve;
      } catch (error) {
        console.error(error);
        return Promise.reject;
      }
    },
    async saveDraftRemote({ state, dispatch, commit }, draftName) {
      commit("setSaveDraftStatus", "loading");
      try {
        const url = `/api/sys/drafts/${draftName}`;
        const data = { text: state.draftFile.text };
        const options = {
          headers: { "x-api-key": state.apikey }
        };
        await dispatch("updateApiKey");
        await api.post(url, data, options);
        await dispatch("setActiveDraft", draftName);
        commit("setSaveDraftStatus", "success");
      } catch (error) {
        commit("setSaveDraftStatus", "error");
        console.error(error);
        // } finally {
        //   commit("setSaveDraftStatus", "normal");
      }
    }
  }
});
